.tooltip {
  opacity: 0;
  transition: opacity 0;
  position: absolute;
  left: 50%;
  margin: 0;
  min-width: 100%;
  // bottom: 69px;
  bottom: calc(100% + 15px);
  text-align: center;
  border-radius: 4px;
  padding: 5px 10px;
  transform: translateX(-50%);
  background-color: rgba(255, 255, 255, 1);
  pointer-events: none;
  z-index: 69;
  box-shadow: 0 0 20px rgba(0,0,0,.2);

  p {
    color: rgba(0,0,0,.8);
    font-size: 12px;

    &:after {
      content: "";
      width: 0;
      height: 0;
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
      border-top: 10px solid #fff;
      position: absolute;
      left: 50%;
      bottom: -7px;
      transform: translateX(-50%);
    }
  }
}

.tooltip_hover {
  position: relative;
  display: inline;

  &:hover {
    .tooltip {
      opacity: 1;
    }
  }
}
