#timeline {
  --line-height: 2px;

  content {

    overflow-x: auto;

    > div {
      display: flex;
      flex-direction: column;
      min-width: 728px !important;

      > div {
        height: calc(50% - (var(--line-height) / 2));
        overflow: hidden;
        position: relative;

        &:first-child {
          section {
            bottom: 0;
            background: linear-gradient(
              0deg,
              rgba(255, 255, 255, 0.1) 0%,
              rgba(255, 255, 255, 0) 100%
            );
            .content {
              bottom: calc(100% + 20px);
              &:after {
                top: 100%;
              }
            }

            &:hover {
              background: linear-gradient(
                0deg,
                rgba(255, 255, 255, 0.15) 0%,
                rgba(255, 255, 255, 0) 100%
              );
            }
          }
        }
        &:last-child {
          section {
            top: 0;
            background: linear-gradient(
              180deg,
              rgba(255, 255, 255, 0.1) 0%,
              rgba(255, 255, 255, 0) 100%
            );
            .content {
              top: calc(100% + 20px);

              &:after {
                top: -100%;
              }
            }

            &:hover {
              background: linear-gradient(
                180deg,
                rgba(255, 255, 255, 0.15) 0%,
                rgba(255, 255, 255, 0) 100%
              );
            }
          }
        }
      }
    }
  }

  section {
    position: absolute;
    height: 20px;

    .content {
      padding: 15px;
      position: absolute;
      right: 0;
      min-width: 222px;
      background-color: rgba(255, 255, 255, 0.1);
      box-shadow: -4px 0px 0px -2px rgba(255, 255, 255, 0.5) inset;

      > * {
        margin: 10px 0;

        &:first-child {
          margin-top: 0;
        }
        &:last-child {
          margin-bottom: 0;
        }
      }

      &:after {
        content: "";
        width: 2px;
        height: 100%;
        position: absolute;
        right: 0;
        background-color: rgba(255, 255, 255, 0.5);
      }
    }

    .dates {
      display: flex;
      justify-content: space-between;
      padding: 0 0px;
      opacity: 0;
      width: calc(100% + 80px);
      transform: translateX(-40px);

      p {
        padding: 0 5px;
        color: white;
        font-size: 14px;
      }
    }

    &:hover {
      .content {
        background-color: rgba(255, 255, 255, 0.15);
      }
      .dates {
        opacity: 0.75;
      }
    }
  }

  #line {
    height: var(--line-height);
    background-color: rgba(255, 255, 255, 0.5);
    width: calc(100% - 11px);
    overflow: visible;

    &:after {
      content: "";
      position: absolute;
      width: 0;
      height: 0;
      right: -11px;
      bottom: -10px;
      border-top: 11px solid transparent;
      border-bottom: 11px solid transparent;
      border-left: 11px solid rgba(255, 255, 255, 0.5);
    }
  }

  h3 {
    font-size: 16px;
    color: rgba(250, 250, 255, 1);
  }

  p {
    font-size: 14px;
  }
}
