#sources content div {
  display: flex;
flex-direction: column;

  > nav {
    width: 100%;
    margin-bottom: 10px;

    ul {
      display: flex;
    }

    li {
      padding: 5px 20px;
      cursor: pointer;
      background-color: rgba($color: #fff, $alpha: 0.1);
    }

    .active {
      background-color: rgba($color: #fff, $alpha: 0.2);
    }
  }

  > ul {
    overflow-y: auto;
    li {
      padding: 5px 20px 5px 0;
      border-bottom: 1px solid rgba($color: #fff, $alpha: 0.1);
      cursor: pointer;
      &:hover {
        background-color: rgba($color: #fff, $alpha: 0.05);
      }

      &:first-child {
        
      border-top: 1px solid rgba($color: #fff, $alpha: 0.1);
      }

      &:before {
        content: " ";
        width: 30px;
        height: 30px;
        display: inline-flex;
        background-size: contain;
        background-repeat: no-repeat;
        vertical-align: -50%;
        margin-right: 7px;
      }

      &.blob:before {
        content:  url("https://raw.githubusercontent.com/Pierre-LHOSTE/Pierre-LHOSTE.github.io/main/files-icons/file.svg");
      }

      &.tree:before {
        content: url("https://raw.githubusercontent.com/Pierre-LHOSTE/Pierre-LHOSTE.github.io/main/files-icons/folder.svg");
      }

      &.error:before {
        content: url("https://raw.githubusercontent.com/Pierre-LHOSTE/Pierre-LHOSTE.github.io/main/files-icons/error.svg");
      }
    }
  }
}
