#simulator content > div {
  display: grid;
  grid-template-areas:
    "input visual"
    "input log";
  grid-template-columns: 33% auto;
  grid-template-rows: 66% auto;
  column-gap: 15px;
  row-gap: 15px;

  > div:nth-child(1) {
    overflow-y: auto;
    grid-area: input;
    background-color: rgba(255, 255, 255, 0.1);
    display: flex;
    flex-direction: column;
    padding: 10px;

    p {
      font-size: 16px;
      color: white;

      &:nth-child(8) {
        font-size: 14px;
        margin-bottom: 20px;
        color: rgba($color: white, $alpha: 0.8);
      }
    }
  }
  > #players {
    grid-area: visual;
    overflow-x: auto;
    display: flex;
    gap: 15px;

    > ul {
      list-style: none;
      //   display: flex;
      //   flex-wrap: wrap;
      width: 50%;
      align-items: center;
    }

    li {
      //   height: 150px;
      //   width: 150px;
      margin: 10px 0;
      display: flex;
      padding: 5px;
      gap: 15px;
      // background-color: rgba($color: white, $alpha: 0.2);

      &:first-child {
        margin-top: 0;
      }

      &:last-child {
        margin-bottom: 0;
      }

      img {
        height: 69px;
        width: auto;
        // margin-right: 15px;
      }

      p {
        color: rgba(255, 255, 255, 0.8);
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
      }
      h1 {
        font-size: 18px;
        font-weight: bold;
        color: white;
      }
      > div {
        overflow: hidden;
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 100%;
        

        > div {
          display: flex;
          gap: 20px;
        }
      }
    }
  }
  > #log {
    grid-area: log;
    overflow-x: auto;
    background-color: rgba(255, 255, 255, 0.1);

    p {
      margin: 5px 0;
    }
  }

  input,
  button {
    border: none;
    background-color: rgba(255, 255, 255, 0.1);
    padding: 5px 10px;
    color: white;
    margin-bottom: 10px;
    margin-top: 5px;

    &:focus-visible {
      outline: none;
      background-color: rgba(255, 255, 255, 0.15);
    }
  }

  button:hover {
    background-color: rgba(255, 255, 255, 0.15);
  }
}
