#contact {
  form {
    height: 100%;
    display: flex;
    flex-direction: row;
    gap: 10px;

    > fieldset {
      display: flex;
      flex-direction: column;
      background-color: rgba($color: #fff, $alpha: 0.1);

      &:nth-child(2) {
        justify-content: space-between;

        fieldset {
            
        flex-grow: 0;
        }
      }
    }
  }
  fieldset {
    border: none;
    flex-grow: 1;
    padding: 10px;
    box-sizing: border-box;

    > * {
      display: block;
      width: 100%;
    }

    input,
    textarea, button {
      border: none;
      background-color: rgba($color: #fff, $alpha: 0.1);
      font-size: 16px;
      outline: none;

      color: white;
      padding: 10px 15px;
      box-sizing: border-box;

      &:hover {
        background-color: rgba($color: #fff, $alpha: 0.15);
        }

        &:focus {
          background-color: rgba($color: #fff, $alpha: 0.3);
        }
    }

    textarea {
      height: 100%;
      resize: none;
    }

    label, p {
      padding: 15px 0 5px 0;

      &:first-child {
        padding-top: 0;
      }
    }

    p {
        color: rgba($color: #fff, $alpha: 0.5);
    }

    button {
        margin-top: 10px;
    }

    /*button {
      height: 100%;
      width: 100%;
      border: none;
      background-color: rgba($color: #fff, $alpha: 0.1);
      color: white;
      font-size: 18px;
      height: auto;
      padding: 10px;

      
      
    }*/


  }
}
