#home {
  #avatar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
  }

  content > div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    > div:first-child {
      display: flex;
      margin-bottom: 20px;
      // margin-right: 50px;

      > div {
        justify-content: center;
        display: flex;
        flex-direction: column;
        padding-left: 40px;
      }

      h3 {
        display: block;
        font-size: 26px;
        padding: 2px 0;

        &:last-child {
          font-size: 24px;
        }
      }
    }

    > * {
      width: 600px;
    }

    p {
      font-size: 18px;
      margin: 10px 0;
    }

    p,
    h3,
    img {
      filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.25));
    }
  }
}
