article {
  min-width: 750px;
  min-height: 450px;
  flex-direction: column;
  resize: both;
  overflow: auto;
  position: absolute;
  --header-height: 33px;
  border-radius: 10px;
  // outline: 10px solid rgba(255, 255, 255, 0.03);
  outline: 2px solid rgba(255, 255, 0, 0.0);
  box-shadow: 0 0 50px -5px rgba(0, 0, 0, 0.75);
  overflow: hidden;
  box-sizing: border-box;

  > header {
    height: var(--header-height);
    background: linear-gradient(45deg, #d000c9 0%, #cc9924 100%);
    display: flex;
    align-items: center;
    justify-content: space-between;
    user-select: none;

    h2 {
      font-size: 16px;
      margin-left: 10px;
      display: flex;
      justify-content: center;

      img {
        height: 15px;
        width: 15px;
        stroke: white;
        background-color: rgba(255, 255, 255, 0.9);
        border-radius: 5px;
        margin-right: 10px;
        padding: 2px;
      }
    }

    button {
      height: 33px;
      width: 33px;
      border: 0;
      border-radius: 0;
      background-color: transparent;
      font-size: 26px;

      &:hover {
        background-color: rgba(255, 255, 255, 0.25);
      }

      &:first-child {
        color: #333;
      }

      &:last-child {
        color: #910b0b;
      }
    }
  }

  > content {
    height: calc(100% - var(--header-height) - 22px);
    padding: 11px;
    width: calc(100% - 22px);
    background: linear-gradient(
      -150deg,
      rgba(0, 0, 0, 0.6) 0%,
      rgba(0, 0, 0, 0.4) 100%
    );
    backdrop-filter: blur(100px);
    overflow: hidden;
    display: block;
    user-select: text;

    > div {
      height: 100%;
      width: 100%;
    }
  }

  &.selected {
    outline: 2px solid rgba(255, 255, 0, 0.25);
  }
}
