@media print {
  #root {
    display: flex;
    flex-direction: column-reverse;
    overflow-y: auto;
    background-image: url(https://media.discordapp.net/attachments/741268256450543618/1030483682005356554/wallpaper_blur.png) !important;
    // align-items: center;
    > div {
      display: none;
    }
  }
  

  article {
    position: static;
    resize: none;
    max-width: auto !important;
    max-height: auto !important;
    background: linear-gradient(
      -150deg,
      rgba(0, 0, 0, 0.6) 0%,
      rgba(0, 0, 0, 0.4) 100%
    );
    outline: none;
    box-shadow: none;
    // backdrop-filter: blur(50px);
    border-radius: 0;
    opacity: 1 !important;
    display: none;
    transform: none !important;

    // min-height: 27vh !important;
    // width: calc(100% - 30px) !important;
    width: calc(100% - 0px) !important;
    padding: 0 15px;
    min-width: 0px !important;

    header {
      background: none;
      // height: 66px;
      // padding-top: 20px;

      > div {
        display: none;
      }
    }
    > content {
      position: static;
      backdrop-filter: none;
      background: none;

      padding: 0;
      width: calc(100%);
      height: calc(100% - var(--header-height) - 5px);
    }
  }

  #home {
    min-height: 21vh !important;
    display: block;
  }

  #social {
    min-height: 20vh !important;
    display: block;
  }

  #timeline {
    min-height: 28vh !important;
    display: block;
  }

  #statistics {
    min-height: 31vh !important;
    display: block;
  }

  #home {
    header {
      display: none;
    }
    grid-area: home;
    content {
      padding: 15px !important;
      width: calc(100% - 30px);
      height: calc(100% - 22px);
      > div {
        display: grid;
        grid-template-areas:
          "profile detail"
          "description description";
        grid-template-rows: auto;
        grid-template-columns: 325px auto;

        p {
          font-size: 16px !important;
        }

        > div {
          width: auto;

          &:first-child {
            margin: 0;
          }
          &:nth-child(1) {
            grid-area: profile;
          }
          &:nth-child(2) {
            grid-area: description;
            p {
              font-size: 20px;
            }
          }
          &:nth-child(3) {
            grid-area: detail;
          }
        }

        * {
          filter: none !important;
        }
      }
    }
  }

  #social {
    h3 {
      font-size: 16px;
    }

    content > div {
      row-gap: 5px;
      column-gap: 5px;
    }

    p {
      font-size: 14px;
      margin: 0 !important;
    }

    ul {
      display: none !important;
    }

    section {
      padding: 10px;
    }
  }

  #timeline {
    content {
      height: calc(100% - var(--header-height) + 10px);
      margin-top: -20px;
      overflow-x: hidden;

      > div {
        > div:last-child {
          section .content {
            top: calc(100% + 5px);
          }

          .dates {
            transform: translateY(5px) !important;
          }
        }
        > div:first-child section .content {
          bottom: calc(100% + 5px) !important;;
        }

        section {
          .dates {
            opacity: 0.25 !important;;

            p:nth-child(2n) {
              opacity: 0 !important;;
            }
          }
          .content > * {
            margin: 0;
          }
        }
      }
    }
  }

  #statistics {
    .chart {
      li {
        min-height: 0px;
        height: 15px;
        margin: 2px 0;
      }
    }

    content {
      > div {
        > div:last-child {
          margin-left: 5px;

          > div:first-child {
            height: calc(60% - 2.5px - 30px);
          }

          > div:last-child {
            margin-top: 5px;
            height: calc(40% - 2.5px - 30px);
          }
        }
      }
    }
  }

  #about,
  #taskbar {
    display: none;
  }
}
