@media screen and (max-width: 750px){
  #taskbar {
    bottom: 0;
    height: 100px;

    ul {
      width: 100%;
      padding: calc(10px + 2vw) 0;
      border-radius: 0;
      justify-content: space-evenly;

      li {
        margin: 0;
        height: auto;
        width: 10%;
        aspect-ratio: 1 / 1;
        max-width: 50px;
      }
    }
  }

  #home {
    content > div > * {
      width: 85%;
    }
  }

  .tooltip {
    display: none;
  }
  #projects content div {
    justify-content: center;
  }

}
