#projects content {
  div {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    align-items: start;
    overflow-y: auto;
  }

  a {
    color: white;
    text-decoration: none;
    display: flex;
    flex-direction: column;
    gap: 16px;
    background-color: rgba(255, 255, 255, 0.1);
    padding: 15px;

    &:hover {
      background-color: rgba(255, 255, 255, 0.15);
    }
  }

  section {
    width: 355px;
    position: relative;
  }

  h3 {
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 16px;

    img {
      height: 32px;
      width: 32px;
    }
  }

  p {
    font-size: 14px;
  }

  a > img {
    width: 100%;
    height: auto;
  }
}
