#taskbar {
  position: absolute;
  bottom: 20px;
    display: flex;
    justify-content: center;
    width: 100%;
    z-index: 465841548;
    pointer-events: none;

  ul {
    display: flex;
    background-color: rgba(255,255,255,.2);
    padding: 10px;
    border-radius: 10px;
    backdrop-filter: blur(5px);
    flex-direction: row-reverse;
    pointer-events: all;

    li {
      width: 50px;
      height: 50px;
      margin: 0 10px;
      background-color: rgba(255,255,255,.75);
      border-radius: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all .1s;
        transform-origin: bottom center;

        img {
          width: 69%;
          height: 69%;
        }

      &:first-child {
        margin-right: 0;
      }
      &:last-child {
        margin-left: 0;
      }

      &:hover {
        transform: scale(1.1);
        // margin: 0 20px;
        background-color: rgba(255,255,255,1);

        &:active {
          transform: scale(1.05);
        }
      }


    }
  }
}
