.icon {
  display: flex;
  align-items: center;
  position: absolute;
  flex-direction: column;
  width: 50px;
  gap: 10px;
  cursor: default;
  padding: 3px 33px;

  div {
    width: 50px;
    height: 50px;
    background-color: rgba(255, 255, 255, 0.75);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  img {
    width: 69%;
  }

  p {
    text-align: center;
    width: 200%;
  }

  &:hover {
    background-color: rgba(255, 255, 255, 0.2);
    outline: 2px solid rgba(255, 255, 255, 0.3);
  }
}
