@import "../components/Window/window.scss";
@import "../components/windows/timeline.scss";
@import "../components/windows/statistics.scss";
@import "../components/windows/home.scss";
@import "../components/windows/social.scss";
@import "../components/simulator/simulator.scss";
@import "../components/tooltip/tooltip.scss";
@import "../components/icon/icon.scss";
@import "../components/taskbar/taskbar.scss";
@import "../components/windows/codesources.scss";
@import "../components/windows/contact.scss";
@import "../components/windows/projects.scss";
@import "./responsive.scss";
@import "./print.scss";

* {
  margin: 0;
  padding: 0;
  font-family: arial;
  transition: opacity 0.5s;
}

ul {
  list-style: none;
}

a {
  color: #7289da;

  &:visited {
    color: #a3b7ff;
  }
}

body {
  overflow: hidden;
  height: 100vh;
  width: 100vw;
  color: white;
}

#root {
  width: 100%;
  height: 100%;
  background: linear-gradient(
    -45deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(10, 0, 94, 1) 100%
  );
  background: url(https://raw.githubusercontent.com/Pierre-LHOSTE/Pierre-LHOSTE.github.io/assets/wallpaper.png);
  background-size: cover;
  background-position: center;
  user-select: none;
}
