#statistics {
  content {
    overflow-x: auto;
    > div {
    min-width: 580px !important;
      display: flex;
      > * {
        width: calc(50% - 10px);

        &:last-child {
          margin-left: 20px;

          display: flex;
          flex-direction: column;

          > * {
            width: calc(100% - 30px);

            &:first-child {
              height: calc(60% - 10px - 30px);
            }

            &:last-child {
              margin-top: 20px;
              height: calc(40% - 10px - 30px);
            }
          }
        }
      }
    }
  }

  .chart {
    background-color: rgba(255, 255, 255, 0.1);
    padding: 15px;
    display: flex;
    flex-direction: column;

    > div {
      height: 100%;
      display: flex;
      margin-top: 10px;
    }

    h3 {
      font-size: 16px;
    }

    ul {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-around;

      &:first-child {
        width: calc(80px + 2%);
      }
      &:last-child {
        width: calc(98% - 80px);

        li {
          background-color: rgba(255, 255, 255, 0.2);

          &:hover {
            background-color: rgba(255, 255, 255, 0.3);
            p {
              opacity: 0.75;
            }
          }

          p {
            opacity: 0;
            font-size: 14px;
            margin-left: calc(100% + 15px);
          }
        }
      }
    }

    li {
      font-size: 14px;
      min-height: 20px;
      display: flex;
      justify-content: left;
      align-items: center;
      white-space: nowrap;
      text-overflow: ellipsis;
overflow: hidden;
    }
  }
}
