#social {
  content > div {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 50% 50%;
    column-gap: 15px;
    row-gap: 15px;
    width: calc(100% - 15px);
    height: calc(100% - 15px);
  }
  section {
    // margin: 0 15px 15px 0;
    background-color: rgba(255, 255, 255, 0.1);
    padding: 15px;
    position: relative;

    ul {
      display: flex;
      height: 50px;

      li {
        position: relative;
        margin: 0 10px;

        img {
          width: 50px;
          height: 50px;
          border-radius: 10px;
          background-color: rgba(255, 255, 255, 1);
          transition: transform 0.1s;
          // padding: 5px;

          &:hover {
            transform: scale(1.1);

            &:active {
              transform: scale(1.05);
            }
          }
        }

        &:first-child {
          margin-left: 0;
        }
      }
    }
  }
  h3 {
    font-size: 20px;
    max-width: 50%;
  }

  section > p {
    color: rgba(255, 255, 255, 0.5);
    margin: 15px 0;
  }
}
